.header {
    &__buttons {
        position: relative;
        display: flex;
        min-width: 270px;
        align-items: center; }

    &__search {
        display: block;
        width: 33px;
        height: 33px;
        border: none;
        border-radius: 50%;
        background: url(/images/icons/search.svg);
        background-color: #b2b2b2;
        background-repeat: no-repeat;
        background-position: top 55% right 9px;
        cursor: pointer;
        &:hover {
            background-color: #998686; }

        &-active {
            padding: 0 10px;
            width: 200px;
            border-radius: 20px;
            color: #fff; } }

    &__login {
        @include button();
        position: relative;
        margin: 0 15px;
        padding: 0 20px 0 0;
        width: 93px;
        height: 32px;
        font-size: 14px;
        text-align: right;
        box-shadow: none;
        background: #b2b2b2;

        &:hover {
            background-color: #998686;
            color: #fff; }

        &::after {
            content: '';
            display: block;
            position: absolute;
            top: calc(50% - 10px);
            left: 10px;
            width: 20px;
            height: 20px;
            background: url(/images/icons/login.svg) no-repeat center; } }

    &__cart {
        @include button();
        position: relative;
        padding: 0 0 0 45px;
        width: 117px;
        height: 32px;
        font-size: 14px;
        text-align: left;
        background: #b2b2b2;
        box-shadow: none;

        &:hover {
            background-color: #998686;
            color: #fff; }

        &::after {
            content: '';
            display: block;
            position: absolute;
            top: calc(50% - 10px);
            left: 10px;
            width: 20px;
            height: 20px;
            background: url(/images/icons/shopping-cart.svg) no-repeat center; } } }
