.promo {
    position: relative;
    min-height: 620px;

    &__image {
        position: absolute;
        top: calc(50% - 375px);
        left: calc(50% - 522px); }

    &__content {
        position: absolute;
        bottom: 60px;
        left: calc(50% - 335px);
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 670px;
        margin: 0 auto;
        text-align: center; }

    &__title {
        font-size: 60px;
        font-weight: 700;
        color: #fff; }

    &__button {
        font-size: 30px;
        font-weight: 700;
        padding: 15px 50px;
        margin: 20px 0 0 0; } }
